<template>
  <q-card flat class="rounded q-pa-sm">
    <q-card-section v-if="!noclose" class="text-right">
      <close-button />
    </q-card-section>
    <q-card-section
      class="text-center bg-transparent q-px-lg flex flex-center q-pb-sm"
      :class="noclose ? 'q-pt-md' : 'q-py-none'"
    >
      <q-stepper
        flat
        animated
        v-model="formStep"
        ref="formStepper"
        color="secondary"
        class="full-width no-unset"
        done-icon="icon-tick text-h4"
        done-color="secondary text-secondary"
        active-icon="icon-circle-01 text-body2"
        active-color="primary"
        inactive-icon="icon-circle-01 text-body2 text-primary-700"
        inactive-color="primary-200"
        error-icon="icon-alert-triangle"
        error-color="negative"
        :contracted="$q.screen.lt.sm"
        header-class="text-primary-700"
        alternative-labels
        style="max-width: 344px"
      >
        <q-step
          :name="1"
          title="Select Plan"
          class="flex flex-center full-width"
          :done="formStep > 1"
          style="max-width: 208px"
        />
        <q-step
          :name="2"
          title="Confirm Plan"
          class="flex flex-center full-width"
          :done="formStep > 1"
          style="max-width: 208px"
        />
      </q-stepper>
    </q-card-section>

    <q-card-section
      class="text-center bg-transparent q-px-lg q-pt-none"
      :class="noclose ? '' : 'q-py-none'"
    >
      <q-stepper
        flat
        animated
        v-model="formStep"
        ref="formStepper"
        color="secondary"
        class="full-width full-inner no-unset with-overflow"
        done-icon="icon-tick text-h4"
        done-color="secondary text-secondary"
        active-icon="icon-circle-01 text-body2"
        active-color="primary"
        inactive-icon="icon-circle-01 text-body2 text-primary-700"
        inactive-color="primary-200"
        error-icon="icon-alert-triangle"
        error-color="negative"
        :contracted="$q.screen.lt.sm"
        header-class="text-primary-700 hidden"
      >
        <q-step
          :name="1"
          title="Select Plan"
          class="flex flex-center full-width full-inner"
          :done="formStep > 1"
        >
          <div class="row q-col-gutter-md">
            <div
              class="col-12 text-body1 font-medium text-primary-700 self-start text-left"
            >
              Add new plan
            </div>
            <div class="col-12">
              <div class="row q-col-gutter-md">
                <div
                  v-for="item in subscription_Talents"
                  :key="item"
                  class="col-12 col-sm-6 col-md-4"
                >
                  <q-card
                    flat
                    v-ripple="{ color: 'secondary' }"
                    class="rounded border-add border-md over-border-secondary"
                    :class="
                      newPlan['talent']?.value === item.value
                        ? 'border-secondary bg-accent'
                        : ''
                    "
                  >
                    <q-radio
                      v-model="newPlan['talent']"
                      v-ripple="{ color: 'secondary' }"
                      :val="item"
                      color="secondary"
                      class="radio-md full-width q-pa-sm"
                    >
                      {{ item.label }}
                    </q-radio>
                  </q-card>
                </div>
              </div>
            </div>
          </div>
          <div class="row q-col-gutter-md q-mt-sm">
            <div
              class="col-12 text-body1 font-medium text-primary-700 self-start text-left"
            >
              Billing
            </div>
            <div class="col-12">
              <div class="row q-col-gutter-md">
                <div
                  v-for="item in subscription_BillingTypes"
                  :key="item"
                  class="col-12 col-sm-6"
                >
                  <q-card
                    flat
                    class="rounded border-add border-md text-body2 hover-border-secondary"
                    :class="
                      newPlan['billing']?.id === item.id
                        ? 'border-secondary bg-accent'
                        : ''
                    "
                  >
                    <q-radio
                      v-model="newPlan['billing']"
                      v-ripple="{ color: 'secondary' }"
                      :val="item"
                      color="secondary"
                      class="radio-md full-width q-pa-sm"
                    >
                      {{ item.label }}
                      <q-badge
                        v-if="
                          item.savings &&
                          planPriceCalculator(
                            newPlan.talent?.value || 1,
                            item.id
                          )?.discount_label?.toLowerCase?.() !== 'custom'
                        "
                        class="badge badge-blue q-ml-xs"
                      >
                        {{ item.savings_label }}
                        {{
                          planPriceCalculator(
                            newPlan.talent?.value || 1,
                            item.id
                          )?.discount_label
                        }}
                      </q-badge>
                    </q-radio>
                  </q-card>
                </div>
              </div>
            </div>
          </div>
          <div class="row q-col-gutter-md q-mt-sm">
            <div
              class="col-12 text-body1 font-medium text-primary-700 self-start text-left"
            >
              Starts on
            </div>
            <div class="col-12">
              <q-input
                dense
                outlined
                unelevated
                v-model="newPlan.start_date"
                @change="startDateInput"
                placeholder="Select date"
                class="hover-text-secondary cursor-pointer"
                style="width: 160px"
              >
                <template v-slot:prepend>
                  <q-icon
                    name="icon-calendar"
                    size="sm"
                    class="hover-text-secondary"
                  />
                </template>
                <q-popup-proxy
                  transition-show="scale"
                  transition-hide="scale"
                  :offset="[-170, -100]"
                  @before-show="proxy.start_date = newPlan.start_date"
                >
                  <q-date
                    today-btn
                    first-day-of-week="1"
                    mask="MMM DD, YYYY"
                    color="secondary"
                    years-in-month-view
                    :options="yearFromToday"
                    v-model="proxy.start_date"
                    :navigation-min-year-month="thisMonth"
                    :navigation-max-year-month="after1Year"
                  >
                    <div class="row items-center justify-end q-gutter-sm">
                      <q-btn
                        outline
                        no-caps
                        unelevated
                        v-close-popup
                        class="button-gm-outline-dark"
                        label="Cancel"
                        color="primary"
                      />
                      <q-btn
                        no-caps
                        unelevated
                        v-close-popup
                        label="Apply"
                        color="secondary"
                        @click="newPlan.start_date = proxy.start_date"
                      />
                    </div>
                  </q-date>
                </q-popup-proxy>
              </q-input>
            </div>
          </div>
          <div class="row q-col-gutter-md q-mt-sm">
            <div
              class="col-12 text-body1 font-medium text-primary-700 self-start text-left"
            >
              Message (Optional)
            </div>
            <div class="col-12">
              <q-input
                dense
                outlined
                rows="4"
                type="textarea"
                placeholder="Enter a description..."
                class="format-bold-light"
                v-model="newPlan.message"
              />
            </div>
          </div>
        </q-step>
        <q-step
          :name="2"
          title="Confirm Plan"
          class="flex flex-center full-width full-inner"
          :done="formStep > 1"
        >
          <div class="q-pb-lg">
            <div class="text-h3 text-primary q-py-xs">
              Confirm your new plan
            </div>
            <div class="text-body1 text-primary-700">
              This plan will be active as well as your current plan.
            </div>
          </div>

          <q-card bordered class="shadows-sm card-padding-sm">
            <div class="q-gutter-lg">
              <div class="text-left text-primary">
                <div class="text-body1 text-primary-500 font-medium">
                  Talent
                </div>
                <div class="text-body1 text-primary font-medium q-pt-xs">
                  {{ newPlan['talent']?.label }}
                </div>
              </div>
              <div class="text-left text-primary">
                <div class="text-body1 text-primary-500 font-medium">
                  Billing
                </div>
                <div class="text-body1 text-primary font-medium q-pt-xs">
                  {{ newPlan['billing']?.label }} / Next Billing on
                  {{ nextBillingDate }}
                </div>
              </div>
              <div class="text-left text-primary">
                <div class="text-body1 text-primary-500 font-medium">
                  Starts on
                </div>
                <div class="text-body1 text-primary font-medium q-pt-xs">
                  {{ formatDate(newPlan?.start_date, 'Do MMMM YYYY') }}
                </div>
              </div>
              <div v-if="newPlan.message" class="text-left text-primary">
                <div class="text-body1 text-primary-500 font-medium">
                  Message
                </div>
                <div
                  class="text-body1 text-primary font-medium q-pt-xs"
                  :class="{
                    ['scroll']: newPlan.message?.length > 30,
                  }"
                  :style="
                    newPlan.message?.length > 30 ? 'max-height: 100px;' : ''
                  "
                >
                  {{ newPlan.message }}
                </div>
              </div>
            </div>
          </q-card>
        </q-step>
      </q-stepper>
    </q-card-section>

    <q-card-actions class="full-width justify-between q-pa-lg">
      <div>
        <div class="text-h3 font-medium">
          {{
            planPriceCalculator(newPlan.talent?.value, newPlan.billing?.id)
              ?.label
          }}
        </div>
        <div class="text-subtitle1 letter-space-normal">
          {{ newPlan['billing']?.billed_label }}
        </div>
      </div>
      <div class="q-gutter-sm">
        <q-btn
          v-if="formStep > 1"
          outline
          no-caps
          unelevated
          class="rounded text-body1 font-medium button-md button-gm-outline-dark"
          :disable="loading"
          @click="formStep -= 1"
        >
          Edit Plan
        </q-btn>
        <q-btn
          no-caps
          unelevated
          color="primary"
          class="rounded slide-hover text-body1 font-medium button-md"
          :disable="loading"
          @click="
            () => {
              if (formStep === 2) {
                requestChanges();
              } else {
                if (allDataIsValid) {
                  formStep += 1;
                } else {
                  checkDataIsValid();
                }
              }
            }
          "
        >
          {{
            formStep === 2
              ? 'Confirm Subscription Upgrade'
              : 'Submit Change Request'
          }}
          <q-icon size="xs" name="icon-arrow-narrow-right" class="q-pl-xs" />
        </q-btn>
      </div>
    </q-card-actions>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import { formatMoney, numberToWords } from 'src/assets/scripts/functions';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import useHelpers from 'stores/helpers';
import fnStore from 'src/stores/fn.store';
import { date } from 'quasar';

export default defineComponent({
  name: 'Dialog_UpgradeYourPlan',

  emits: ['requestChanges'],

  props: {
    noclose: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      newPlan: { talent: {}, billing: {}, start_date: null, message: null },
      proxy: { start_date: null },
      numberToWords,
      formatMoney,
      formatDate: date.formatDate,
      formStep: 1,
      loading: false,
    };
  },

  computed: {
    ...mapState(useUser, ['user', 'selectedOrg', 'selectedOrgIsMaintenance']),
    ...mapState(useGrowmodo, [
      'maintenance',
      'subscription',
      'subscription_BillingTypes',
      'subscription_Talents',
    ]),
    subscription_billing_id() {
      return this.orgsubscription?.subscription_billing_id;
    },
    subscription_talent_id() {
      return this.orgsubscription?.subscription_talent_id;
    },
    thisMonth() {
      return date.formatDate(this.$root.realtimeDate, 'YYYY/MM');
    },
    after1Year() {
      return date.formatDate(
        date.addToDate(this.$root.realtimeDate, { years: 1 }),
        'YYYY/MM'
      );
    },
    orgsubscription() {
      return !fnStore.getAuth() ? {} : this.selectedOrg?.subscriptions;
    },
    allDataIsValid() {
      return (
        this.newPlan?.talent?.value &&
        this.newPlan?.billing?.value &&
        this.newPlan?.start_date &&
        date.isValid(this.newPlan?.start_date) &&
        this.yearFromToday(
          date.formatDate(this.newPlan?.start_date, 'YYYY/MM/DD')
        )
      );
    },
    nextBillingDate() {
      return this.allDataIsValid
        ? date.formatDate(
            date.addToDate(this.newPlan.start_date, {
              months: this.newPlan?.['billing']?.value,
            }),
            'Do MMMM YYYY'
          )
        : 'N/A';
    },
  },

  methods: {
    ...mapActions(useGrowmodo, ['planPriceCalculator']),
    ...mapActions(useHelpers, ['getCurrency']),
    ...mapActions(useUser, ['requestSubscriptionChange']),

    checkDataIsValid() {
      if (this.allDataIsValid) return true;
      let message = '';
      if (!this.newPlan?.talent?.value) {
        message = 'Please select talent count';
      } else if (!this.newPlan?.billing?.value) {
        message = 'Please select billing';
      } else if (!this.newPlan?.start_date) {
        message = 'Please select start date';
      } else if (!date.isValid(this.newPlan?.start_date)) {
        message = 'Invalid start date selected';
      } else if (
        !this.yearFromToday(
          date.formatDate(this.newPlan?.start_date, 'YYYY/MM/DD')
        )
      ) {
        message = 'Start date is out of range';
      }

      if (message) {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message,
        });
        return false;
      }
      return true;
    },

    async requestChanges() {
      this.loading = true;
      if (this.checkDataIsValid()) {
        const _ = this.newPlan;
        const response = await this.requestSubscriptionChange({
          subscription_talent_id: _.talent?.id,
          subscription_billing_id: _.billing?.id,
          starts_on: _.start_date,
          message: _.message,
        });
        if (response.success) {
          this.$root.dialogRequestUpgrade = true;
          this.$emit('requestChanges', this.newPlan);
        }
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: 'Please select you new plan details.',
        });
      }
      this.loading = false;
    },

    yearFromToday(d) {
      return (
        d >= date.formatDate(this.$root.realtimeDate, 'YYYY/MM/DD') &&
        d <=
          date.formatDate(
            date.addToDate(this.$root.realtimeDate, { year: 1 }),
            'YYYY/MM/DD'
          )
      );
    },

    startDateInput(e) {
      if (date.isValid(e)) {
        this.newPlan.start_date = date.formatDate(e, 'MMM DD, YYYY');
      }
    },
  },
});
</script>
